
var app;
export default {
	onLaunch: function() {
		console.log('App Launch')
		app = getApp();
		uni.removeStorage({
			key: 'accountInfo'
		})
	},
	onShow: function() {
		console.log('App Show')
		uni.getSystemInfoSync({
			success(res) {
				app.globalData.systemInfo = res
			}
		})
	},
	onHide: function() {
		console.log('App Hide')
	}
};
